.language-switcher {
  display: flex;
  gap: 8px;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.1);
  padding: 8px 12px;
  border-radius: 10px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  width: 200px;
  height: 44px;
}

.lang-button {
  padding: 8px 14px;
  border: 2px solid transparent;
  border-radius: 8px;
  background: transparent;
  color: rgba(255, 255, 255, 0.6);
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  letter-spacing: 0.5px;
  flex: 1;
  text-align: center;
  white-space: nowrap;
}

.lang-button:hover:not(.active) {
  background: rgba(217, 119, 6, 0.15);
  color: rgba(255, 255, 255, 0.95);
  border-color: rgba(217, 119, 6, 0.3);
  transform: translateY(-1px);
}

.lang-button.active {
  background: var(--color-accent);
  color: white;
  box-shadow: 0 4px 12px rgba(217, 119, 6, 0.5), inset 0 2px 4px rgba(0, 0, 0, 0.2);
  font-weight: 700;
  border-color: var(--color-accent);
}

/* Responsive design */
@media (max-width: 768px) {
  .language-switcher {
    gap: 4px;
    padding: 6px 8px;
    width: 180px;
    height: 40px;
  }

  .lang-button {
    padding: 6px 8px;
    font-size: 12px;
  }
}

@media (max-width: 480px) {
  .language-switcher {
    width: 160px;
    height: 38px;
  }

  .lang-button {
    padding: 5px 6px;
    font-size: 11px;
  }
}
